import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import './sets.scss'
import classNames from 'classnames'
import { Link } from "react-router-dom";

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { getSets, postSet, deleteSet } from '../../services/backend'
import Spinner from '../../components/spinner'
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';


export default function Sets (props) {

  const [setsList, setSetsList] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [newSetName, setNewSetName] = useState('');

  useEffect(() => {
    setIsLoading(true);
    getSets().then((response) => {
      setSetsList(response.data);
      setIsLoading(false);
    })
  }, []);

  function onDeleteSet(setId, setName) {
    let confirm = window.confirm(`Confirm deleting "${setName}" set`);
    if (!confirm) return;
    setIsLoading(true);
    deleteSet(setId)
      .then(response => {
        setSetsList(response.data);
        setIsLoading(false);
      })
      .catch(e => {
        console.log(e);
        setIsLoading(false);
      })
  }

  function onAddSet(name) {
    setIsLoading(true);
    postSet({name}).then(response => {
      setSetsList(response.data);
      setIsLoading(false);
      setNewSetName('');
    })
  }

  return (
    <Paper className="Paper-main Sets">
      <h2>Art Sets</h2>
      {isLoading && <Spinner />}
      {!isLoading &&
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Set name</TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {setsList.map(row => (
            <TableRow className="table-row" hover={true} key={row._id}
                      onClick={() => props.history.push('/' + row._id)}>
              <TableCell component="td" scope="row">
                {row.name}
              </TableCell>
              <TableCell onClick={e => e.stopPropagation()} align='right' className='delete-button-cell'>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={() => onDeleteSet(row._id, row.name)}
                  className='delete-button'
                >
                  <DeleteIcon /> Delete
                </Button>
              </TableCell>
            </TableRow>
          ))}
          <TableRow className="table-row" hover={true}
                    onClick={() => {}}>
            <TableCell component="td" scope="row" colspan="100%">
              <div className="add-form-wrapper">
                <TextField
                  id="title"
                  label="Title"
                  value={newSetName}
                  onChange={(e) => setNewSetName(e.target.value)}
                  margin="normal"
                  variant="outlined"
                  className="add-input"
                />
                <div>
                  <Button variant="contained" color="primary" onClick={() => onAddSet(newSetName)}>
                    <AddIcon /> Add new set
                  </Button>
                </div>
              </div>
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
      }
    </Paper>
  )
}
