import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './layer-items.scss'
import Paper from '@material-ui/core/Paper';
import { getLayerItems, postLayerItemsImages, addLayer } from '../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../components/spinner";
import Colors from './colors';
import Images from './images'
import Form from './form'
import Button from "@material-ui/core/Button";

export default function LayerItemsAdd (props) {

  const [isLoading, setIsLoading] = useState(false);

  function onAddLayer(data) {
    return addLayer(props.match.params.setId, data).then((response) => {
      props.history.push('/' + props.match.params.setId + '/' + response.data.id)
    })
  }

  return (
    <Paper className='Paper-main LayerItems'>
      <Button color='primary' onClick={props.history.goBack}>Back to Layers</Button>
      <h2>Add New Layer</h2>
      <div className="form-container">
        <Form
          match={props.match}
          data={{}}
          onSubmit={onAddLayer}
        />
      </div>
    </Paper>
  )
}
