import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './layer-items.scss'
import Paper from '@material-ui/core/Paper';
import {
  getLayerItems,
  postLayerItemsImages,
  deleteLayerItemImage,
  patchLayerItem,
  addLayer
} from '../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../components/spinner";
import Colors from './colors';
import Images from './images'
import Form from './form'
import Button from '@material-ui/core/Button';
import {toast} from "react-toastify";

export default function LayerItems (props) {

  const [layerData, setLayerData] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    getLayerItems(props.match.params.setId, props.match.params.id).then((response) => {
      setLayerData(response.data);
      setIsLoading(false);
    })
  }, [props.match.params]);

  function onImageSubmit(acceptedFiles) {
    let filesToSend = acceptedFiles;
    setIsLoading(true);
    postLayerItemsImages(props.match.params.setId, props.match.params.id, filesToSend)
      .then((response) => {
        setIsLoading(false);
        toast.success('Image was successfully added');
        setLayerData(response.data);
      })
  }

  function onImageDelete(imageId) {
    setIsLoading(true);
    deleteLayerItemImage(props.match.params.setId, props.match.params.id, imageId)
      .then((response) => {
        setIsLoading(false);
        toast.info('Image was successfully deleted');
        setLayerData(response.data);
      })
  }

  function onPatchLayer(data) {
    return patchLayerItem(props.match.params.setId, props.match.params.id, data)
  }

  return (
    <Paper className='Paper-main LayerItems'>
      <Button color='primary' onClick={props.history.goBack}>Back to Layers</Button>
      <div className="form-container">
        <Form
          match={props.match}
          data={layerData}
          onSubmit={onPatchLayer}
        />
      </div>
      <Colors
        match={props.match}
        data={layerData}
        setLayerData={setLayerData}
      />
      <Images
        data={layerData}
        onImageDelete={onImageDelete}
        onImageSubmit={onImageSubmit}
        isLoading={isLoading}
      />
    </Paper>
  )
}
