import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import './layers.scss'
import classNames from 'classnames'
import { Link } from "react-router-dom";

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { getLayers, deleteLayer } from '../../services/backend'
import Spinner from '../../components/spinner'
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/Delete';


export default function Layers (props) {

  const [tableData, setTableData] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    getLayers(props.match.params.setId).then((response) => {
      setTableData(response.data);
      setIsLoading(false);
    })
  }, []);

  function onDeleteLayer(id, title) {
    let confirm = window.confirm(`Confirm deleting "${title}" layer`);
    if (!confirm) return;
    setIsLoading(true);
    deleteLayer(props.match.params.setId, id).then(response => {
      setTableData(response.data);
      setIsLoading(false);
    })
  }

  return (
    <Paper className="Paper-main Layers">
      <Button color='primary' onClick={props.history.goBack}>Back to Sets</Button>
      <h2>Layers</h2>
      {isLoading && <Spinner />}
      {!isLoading &&
      <Table>
        <TableHead>
          <TableRow>
            {/*<TableCell>ID</TableCell>*/}
            <TableCell>Title</TableCell>
            <TableCell>Z-index</TableCell>
            <TableCell>Colors</TableCell>
            <TableCell>Images quantity</TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {tableData.layers && tableData.layers.map(row => (
            <TableRow className="table-row" hover={true} key={row._id}
                      onClick={() => props.history.push(props.match.params.setId + '/' + row._id)}>
              {/*<TableCell component="th" scope="row">*/}
              {/*  {row._id}*/}
              {/*</TableCell>*/}
              <TableCell component="th" scope="row">
                {row.title}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.zIndex}
              </TableCell>
              <TableCell component="th" scope="row">
                {(row.colors && row.colors.length) ?
                  row.colors.map(color => (
                    <div className="color" style={{background: color}}></div>
                  )) :
                  'No colors'
                }
              </TableCell>
              <TableCell component="th" scope="row">
                {row.images.length}
              </TableCell>
              <TableCell onClick={e => e.stopPropagation()}>
                <Button
                  variant="contained"
                  color="secondary"
                  onClick={() => onDeleteLayer(row._id, row.title)}
                  className="delete-button"
                >
                  <DeleteIcon /> Delete
                </Button>
              </TableCell>
            </TableRow>
          ))}
          <TableRow className="table-row" hover={true}
                    onClick={() => {props.history.push(props.match.params.setId + '/add')}}>
            <TableCell component="th" scope="row" colspan="100%">
              <strong>
                Add new layer
              </strong>
            </TableCell>
          </TableRow>
        </TableBody>
      </Table>
      }
    </Paper>
  )
}
