import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './colors.scss'
import Paper from '@material-ui/core/Paper';
import {postLayerItemColor, deleteLayerItemColor} from '../../../services/backend'
import { Link } from "react-router-dom";
import Spinner from "../../../components/spinner";
import ColorPicker from '../../../components/material-ui-color-picker'
import {Formik} from "formik";
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add'
import DeleteIcon from '@material-ui/icons/Delete';
import {toast} from "react-toastify";


export default function Colors (props) {

  const [newColor, setNewColor] = useState('#ffffff');

  return (
    <div className="Colors">
      <h2>Colors</h2>
      {props.data && props.data.colors && props.data.colors.map(color => (
       <div className="color" style={{background: color}} onClick={() => {
         deleteLayerItemColor(props.match.params.setId, props.match.params.id, color).then(response => {
           props.setLayerData(response.data)
           toast.info('Color was successfully deleted');
         })
       }}>
         <div className="darken-layout"></div>
         <div className="delete-icon-wrapper">
           <DeleteIcon />
         </div>
       </div>
      ))}
      <Formik
        enableReinitialize
        initialValues={{color: '#ffffff'}}
        onSubmit={(values, { setSubmitting }) => {
          postLayerItemColor(props.match.params.setId, props.match.params.id, newColor).then(response => {
            setSubmitting(false);
            toast.success(`Color ${newColor} was successfully added`)
            props.setLayerData(response.data)
          })
        }}
      >
        {props => {
          const {
            values,
            errors,
            touched,
            isSubmitting,
            handleChange,
            handleSubmit,
          } = props;
          return (
            <form onSubmit={handleSubmit}>
              <p>
                <ColorPicker
                  label="Add new color"
                  id='color'
                  name='color'
                  value={newColor}
                  onChange={val => setNewColor(val)}
                />
              </p>
              <p>
                <Button variant="contained" type="submit" color="primary" disabled={isSubmitting}>
                  <AddIcon /> Add color
                </Button>
              </p>
            </form>
          )
        }}
      </Formik>
    </div>
  )
}
