import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/';
console.log(API_URL);

const axiosInstance = axios.create({
  headers: {
    'Content-Type': 'application/json',
  }
});

const axiosFilesInstance = axios.create({
  headers: {
    'Content-Type': 'multipart/form-data',
  }
});

// SETS

export function getSets() {
  return axiosInstance.get(API_URL + 'admin/sets');
}

export function postSet(data) {
  return axiosInstance.post(API_URL + 'admin/sets', data);
}

export function deleteSet(setId) {
  return axiosInstance.delete(API_URL + 'admin/sets/' + setId);
}

// LAYERS

export function getLayers(setId) {
  return axiosInstance.get(API_URL + 'admin/' + setId + '/layers');
}

export function addLayer(setId, data) {
  return axiosInstance.post(API_URL + 'admin/' + setId + '/layers/', data);
}

export function deleteLayer(setId, id) {
  return axiosInstance.delete(API_URL + 'admin/' + setId + '/layers/' + id);
}

export function getLayerItems(setId, id) {
  return axiosInstance.get(API_URL + 'admin/' + setId + '/layers/' + id);
}

export function patchLayerItem(setId, id, data) {
  return axiosInstance.patch(API_URL + 'admin/' + setId + '/layers/' + id, data);
}

// COLORS

export function postLayerItemColor(setId, id, color) {
  return axiosInstance.post(API_URL + 'admin/' + setId + '/layers/' + id + '/colors', {color});
}
//props.match.params.setName

export function deleteLayerItemColor(setId, id, color) {
  color = color.replace('#', '');
  return axiosInstance.delete(API_URL + 'admin/' + setId + '/layers/' + id + '/colors/' + color);
}

export function postLayerItemsImages(setId, id, filesToSend) {
  let data = new FormData();
  filesToSend.forEach(file => data.append('images', file));
  data.append('name', 'images');

  return axiosFilesInstance.post(API_URL + 'admin/' + setId + '/layers/' + id + '/images', data);
}


export function deleteLayerItemImage(setId, id, imageId) {
  return axiosInstance.delete(API_URL + 'admin/' + setId + '/layers/' + id + '/images/' + imageId);
}
