import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './images.scss'
import Paper from '@material-ui/core/Paper';
import { getLayerItems, postLayerItemsImages, deleteLayerItemImage } from '../../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../../components/spinner";
import DeleteIcon from '@material-ui/icons/Delete';


export default function Images (props) {

  return (
    <div className="Images">
      <h2>Layer images</h2>
      {props.isLoading && <Spinner position="absolute"/>}
      <div>
        {props.data.images && props.data.images.map((image) => (
          <div className="image-container">
            <div className="image-wrapper" onClick={() => props.onImageDelete(image._id)}>
              <img src={image.value} alt=""/>
              <div className="delete-icon-wrapper">
                <DeleteIcon />
              </div>
            </div>
          </div>
        ))}
      </div>
      <Dropzone multiple={true} onDrop={acceptedFiles => props.onImageSubmit(acceptedFiles)}>
        {({getRootProps, getInputProps}) => (
          <div className="container">
            <div className="dropzone" {...getRootProps()}>
              <input {...getInputProps()} />
              <p>Drag 'n' drop some files here, or click to select files</p>
            </div>
          </div>
        )}
      </Dropzone>
    </div>
  )
}
